
import React from 'react';
import {connect} from 'react-redux';
import vkconnect from '@vkontakte/vkui-connect';
import {bindActionCreators} from 'redux'
import {goBack, closeModal, setStory} from "./js/store/router/actions";
import * as VK from './js/services/VK';

import {Epic, View, Root, Tabbar, Group, Div, ModalRoot, TabbarItem, PanelSpinner, ConfigProvider} from "@vkontakte/vkui";

import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon28Game from '@vkontakte/icons/dist/28/game';
import Icon24Education from '@vkontakte/icons/dist/24/education';
import Icon24LogoLivejournal from '@vkontakte/icons/dist/24/logo_livejournal';
import Icon24AddOutline from '@vkontakte/icons/dist/24/add_outline';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon24Note from '@vkontakte/icons/dist/24/note';
import Icon24Article from '@vkontakte/icons/dist/24/article';
import Icon24Write from '@vkontakte/icons/dist/24/write';
import imgtest from './js/panels/error.png';
/*-----------------------------------------------------------*/
import HomePanelBase from './js/panels/home/base';
import HomePanelGroups from './js/panels/home/groups';
/*-----------------------------------------------------------*/
import HomePanelZvania999 from './js/panels/more/zvania999';
/*-----------------------------------------------------------*/
import HomePanelMassa from './js/panels/home/massa9';
import HomePanelMassa1 from './js/panels/home/massa91';
import HomePanelMassa2 from './js/panels/home/massa92';
/*------------------------------------------------------------*/
import HomePanelMassovaya from './js/panels/home/massovaya';
import HomePanelMassovaya1 from './js/panels/home/massovaya1';
import HomePanelMassovaya2 from './js/panels/home/massovaya2';
/*------------------------------------------------------------*/
import HomePanelPlotnost from './js/panels/home/plotnost';
import HomePanelPlotnost1 from './js/panels/home/plotnost1';
/*------------------------------------------------------------*/
import HomePanelKolichestvo from './js/panels/home/kolichestvo';
import HomePanelKolichestvo1 from './js/panels/home/kolichestvo1';
import HomePanelKolichestvo2 from './js/panels/home/kolichestvo2';
import HomePanelKolichestvo3 from './js/panels/home/kolichestvo3';
/*------------------------------------------------------------*/
import HomePanelFull from './js/panels/home/full';
/*------------------------------------------------------------*/
import HomePanelKoncent from './js/panels/home/koncent';
import HomePanelKoncent1 from './js/panels/home/koncent1';
/*------------------------------------------------------------*/
import HomePanelObiem from './js/panels/home/obiem';
import HomePanelObiem1 from './js/panels/home/obiem1';
import HomePanelObiem2 from './js/panels/home/obiem2';
/*------------------------------------------------------------*/
import HomePanelZnach from './js/panels/home/znach';
/*------------------------------------------------------------*/
import HomePanelAdminka99 from './js/panels/home/adminka99';
/*------------------------------------------------------------*/
import MorePanelBase from './js/panels/more/base';
import MorePanelExample from './js/panels/more/example';
import ZvaniaPanelBase from './js/panels/zvania999/base';
import AddPanelBase from './js/panels/add/base';
import ReshPanelBase from './js/panels/resh/base';
import HomeBotsListModal from './js/components/modals/HomeBotsListModal';
import HomeBotInfoModal from './js/components/modals/HomeBotInfoModal';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24FavoriteOutline from '@vkontakte/icons/dist/24/favorite_outline';
import Icon24User from '@vkontakte/icons/dist/24/user';
/*-------------------------------------------------------------*/
var theme = 'state.vkui.colorScheme';


class App extends React.Component {
    constructor(props) {
        super(props);

        this.lastAndroidBackAction = 0;
    }


  state = {
        showImg: false,
        fetchedUser: "",
        teh: '',
        theme: 'state.vkui.colorScheme',
        viewLoaded: null
    };

    componentDidMount() {
        const {goBack, dispatch, colorScheme} = this.props;
        dispatch(VK.initApp());

        vkconnect.subscribe((e) => {
        switch(e.detail.type) {
case 'VKWebAppGetUserInfoResult':
this.setState({ fetchedUser: e.detail.data });
break;
}})

console.log(this.state.fetchedUser);


        window.onpopstate = () => {
            let timeNow = +new Date();

            if (timeNow - this.lastAndroidBackAction > 500) {
                this.lastAndroidBackAction = timeNow;

                goBack('Android');
            } else {
                window.history.pushState(null, null);
            }
        };
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        const {activeView, activeStory, activePanel, scrollPosition} = this.props;

        if (
            prevProps.activeView !== activeView ||
            prevProps.activePanel !== activePanel ||
            prevProps.activeStory !== activeStory
        ) {
            let pageScrollPosition = scrollPosition[activeStory + "_" + activeView + "_" + activePanel] || 0;

            window.scroll(0, pageScrollPosition);
        }
    }

    render() {
        const {goBack, setStory, closeModal, popouts, activeView, activeStory, activePanel, activeModals, panelsHistory, colorScheme} = this.props;

        let history = (panelsHistory[activeView] === undefined) ? [activeView] : panelsHistory[activeView];
        let popout = (popouts[activeView] === undefined) ? null : popouts[activeView];
        let activeModal = (activeModals[activeView] === undefined) ? null : activeModals[activeView];

        const homeModals = (
            <ModalRoot activeModal={activeModal}>
                <HomeBotsListModal
                    id="MODAL_PAGE_BOTS_LIST"
                    onClose={() => closeModal()}
                />
                <HomeBotInfoModal
                    id="MODAL_PAGE_BOT_INFO"
                    onClose={() => closeModal()}
                />
            </ModalRoot>
        );
        return (
            <ConfigProvider isWebView={true} scheme={colorScheme}>

                <Epic activeStory={activeStory} tabbar={
                    <Tabbar>
                        <TabbarItem
                            onClick={() => setStory('home', 'base')}
                            selected={activeStory === 'home'}
                         text='Игры'><Icon24Coins/></TabbarItem>
                         <TabbarItem
                         onClick={() => setStory('add', 'AddBase')}
                         selected={activeStory === 'add'}
                         text='Топ'><Icon24FavoriteOutline/></TabbarItem>
                                  <TabbarItem
                            onClick={() => setStory('more', 'callmodal')}
                            selected={activeStory === 'more'}
                         text='Профиль'><Icon24User/></TabbarItem>
                    </Tabbar>
                }>

                    <Root id="home" activeView={activeView} popout={popout}>
                        <View
                            id="home"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <HomePanelBase id="base" withoutEpic={false}/>
                            <HomePanelGroups id="groups"/>
                            <HomePanelZvania999 id="zvania999"/>
                            <HomePanelMassa id="massa9"/>
                            <HomePanelMassa1 id="massa91"/>
                            <HomePanelMassa2 id="massa92"/>
                            <HomePanelMassovaya id="massovaya"/>
                            <HomePanelMassovaya1 id="massovaya1"/>
                            <HomePanelMassovaya2 id="massovaya2"/>
                            <HomePanelPlotnost id="plotnost"/>
                            <HomePanelPlotnost1 id="plotnost1"/>
                            <HomePanelKolichestvo id="kolichestvo"/>
                            <HomePanelKolichestvo1 id="kolichestvo1"/>
                            <HomePanelKolichestvo2 id="kolichestvo2"/>
                            <HomePanelKolichestvo3 id="kolichestvo3"/>
                            <HomePanelFull id="full"/>
                            <HomePanelKoncent id="koncent"/>
                            <HomePanelKoncent1 id="koncent1"/>
                            <HomePanelObiem id="obiem"/>
                            <HomePanelObiem1 id="obiem1"/>
                            <HomePanelObiem2 id="obiem2"/>
                            <HomePanelZnach id="znach"/>
                            <HomePanelAdminka99 id="adminka99"/>
                                            </View>
                    </Root>
                    <Root id="more" activeView={activeView} popout={popout}>
                        <View
                            id="more"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <MorePanelBase id="callmodal"/>
                        </View>
                        <View
                            id="modal"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <MorePanelExample id="filters"/>
                        </View>
                    </Root>




 <Root id="zvania" activeView={activeView} popout={popout}>
                        <View
                            id="zvania"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <ZvaniaPanelBase id="ZvaniaBase"/>
                        </View>
                        <View
                            id="modal"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <MorePanelExample id="filters"/>
                        </View>
                    </Root>
 <Root id="resh" activeView={activeView} popout={popout}>
                        <View
                            id="resh"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <ReshPanelBase id="ReshBase"/>
                        </View>
                        <View
                            id="modal"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <MorePanelExample id="filters"/>
                        </View>
                    </Root>
 <Root id="add" activeView={activeView} popout={popout}>
                        <View
                            id="add"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <AddPanelBase id="AddBase"/>
                        </View>
                        <View
                            id="modal"
                            modal={homeModals}
                            activePanel={activePanel}
                            history={history}
                            onSwipeBack={() => goBack()}
                        >
                            <MorePanelExample id="filters"/>
                        </View>
                    </Root>





                </Epic>
            </ConfigProvider>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        activeView: state.router.activeView,
        activePanel: state.router.activePanel,
        activeStory: state.router.activeStory,
        panelsHistory: state.router.panelsHistory,
        activeModals: state.router.activeModals,
        popouts: state.router.popouts,
        scrollPosition: state.router.scrollPosition,

        colorScheme: state.vkui.colorScheme
    };
};


function mapDispatchToProps(dispatch) {
    return {
        dispatch,
        ...bindActionCreators({setStory, goBack, closeModal}, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
