import React from 'react';
import {connect} from 'react-redux';
import vkconnect from '@vkontakte/vkui-connect';
import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import {Div, Panel, Alert, Group, Button, Cell, Tabs, CellButton, FormLayout, Input, Select, Search, List, PanelHeader} from "@vkontakte/vkui";
import Icon24Write from '@vkontakte/icons/dist/24/write';
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
class HomePanelBase extends React.Component {
    state = {
        showImg: false,
        value1: "",
        value2: "",
        res: "Вы еще не произвели расчет.",
        res2: " ",
        valid: '',
        popout: '',
        input1: '',
        input2: '' 
    };
onChanges = (e) => {
const { name, value } = e.currentTarget;
this.setState({ [name]: value });
}
    showImg = () => {
        this.setState({showImg: true});
    };
openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Ясно',
                    autoclose: true,
                    style: 'cancel',
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2>Информация</h2>
                <p>Действие «Копирование» невозможно, т.к. расчет не был произведен или возникла ошибка.</p>
            </Alert>
        );
    }
clear = () => {
this.setState({value1:'',value2:''})
}
copy = () => {
if(this.state.res == "Вы еще не произвели расчет."){
this.openPopout();
}else{
vkconnect.send("VKWebAppCopyText", {text: this.state.res});

}
if(this.state.res == "Произошла ошибка счета. Проверьте введенные данные."){
this.openPopout();
}else{
connect.send("VKWebAppCopyText", {text: this.state.res});

}
}
openras() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Очистить',
                    autoclose: true,
                    style: 'cancel',
                    action: this.clear,
                },{
                    title: 'Скопировать',
                    autoclose: true,
                    style: 'cancel',
                    action: this.copy,
                }]}
                onClose={() => this.props.closePopout()}>
                <h2>Информация</h2>
                <p>Выберите нужное Вам действие:</p>
            </Alert>
        );
    }
num = () => {
var result;
var result2;
if(this.state.value1 > 0 & this.state.value1 <= 99999){
if(this.state.value2 <= 99999 & this.state.value2 > 0){
if(this.state.value1 && this.state.value2){
result = (this.state.value1 / this.state.value2).toFixed(3);
result2 = result / 1000;
this.setState({res: "Плотность(p) = "  + result+ " г/см³"});
this.setState({valid: 'default'});
vkconnect.send("VKWebAppTapticImpactOccurred", {"style": "light"});
}else{
this.setState({res: "Произошла ошибка счета. Проверьте введенные данные."});
this.setState({valid: 'error'});
}
}else{
this.setState({res: "Произошла ошибка счета. Проверьте введенные данные."});
this.setState({valid: 'error'});
}
}else{
this.setState({res: "Произошла ошибка счета. Проверьте введенные данные."});
this.setState({valid: 'error'});
}
console.log(this.state.res);
}


    render() {
        const {id, setPage, withoutEpic} = this.props;
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={this.props.goBack} />}>
  Формулы
</PanelHeader>
<Group title="Как посчитать?">
<Div>
<p oncopy="return false;">Введите в поля ввода свои значения и нажмите на кнопку "Вычислить". </p>
</Div>
</Group>


<Group title="Формула">
<Div>
Формула нахождения: <b>p=m/V</b>
</Div>
</Group>

<Group title="Вычислитель">
<Div>



<Input
value={this.state.value1}
type="number"
top="Значение хим.количества(n)"
name="value1"
placeholder="Значение массы(m)"
onChange={this.onChanges}
status={this.state.value1 ? 'valid' : this.state.valid}onKeyDown={this.func}
/>

<br/>

<Input
value={this.state.value2}
type="number"
top="Значение мол.массы(M)"
name="value2"
placeholder="Значение объема(V)"
onChange={this.onChanges}
status={this.state.value2 ? 'valid' : this.state.valid}
onKeyDown={this.func}
/>

<br/>


<Button size="xl" level="commerce" onClick={(this.num)}>Вычислить</Button>
</Div>
</Group>
<Group title="Результат">
<CellButton onClick={() => this.openras()}>{this.state.res}</CellButton>
</Group>
</Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
