import React from 'react';
import {connect} from 'react-redux';
import vkconnect from '@vkontakte/vkui-connect';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";
import Icon28Message from '@vkontakte/icons/dist/28/message';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import Icon28MessagesOutline from '@vkontakte/icons/dist/28/messages_outline';
import Icon28UserAddOutline from '@vkontakte/icons/dist/28/user_add_outline';
import Icon24Game from '@vkontakte/icons/dist/24/game';
import Icon24Forward from '@vkontakte/icons/dist/24/forward';
import Icon24UserAdd from '@vkontakte/icons/dist/24/user_add';
import Icon24Notification from '@vkontakte/icons/dist/24/notification';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon16Pin from '@vkontakte/icons/dist/16/pin';
import Icon12Fire from '@vkontakte/icons/dist/12/fire';
import Icon36Cancel from '@vkontakte/icons/dist/36/cancel';
import {
    Div,
    Panel,
    Group,
    CellButton,
    PanelHeader,
    Alert,
    Switch,
    Footer,
    FixedLayout,
    Button,
    Snackbar,
    HeaderButton,
    Avatar,
    Checkbox,
    Link,
    Cell,
    List,
    PanelSpinner,
    PanelHeaderBack,
    Radio,
    Separator,
    HorizontalScroll,
    TabsItem,
    Tabs
} from "@vkontakte/vkui";
const blueBackground = {
  backgroundColor: 'var(--accent)'
};
class HomePanelProfile extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activeTab: props.activeTab["EXAMPLE"] || "modal",
            theme: ''
        };
    }

    setTab(tab) {
        this.setState({
            activeTab: tab
        });
        this.openBase = this.openBase.bind(this);
    }	    componentWillUnmount() {
        const {setScrollPositionByID, setActiveTab} = this.props;
        setActiveTab("EXAMPLE", this.state.activeTab);
        setScrollPositionByID("EXAMPLE_TABS_LIST");
    }
    componentDidMount(){
restoreScrollPosition();
fetch(`https://rudenok.000webhostapp.com/api/apii.php?${window.location.search.replace("?", "")}&method=theme.get`)
.then(res => res.json())
.then(res => { this.setState({theme: res.response})})
.catch(e => {
if(this.state.snackbar) return;
    this.setState({ snackbar:
      <Snackbar
        layout="vertical"
        onClose={() => this.setState({ snackbar: null })}
        before={<Avatar size={24} style={blueBackground}><Icon36Cancel fill="#fff" width={18} height={18} /></Avatar>}>
       Мы не можем получить данные. 
Возможно, пропало подключение к интернету. Попробуйте перезайти на вкладку.
      </Snackbar>
    });
})
}

themec = () => {
fetch(`https://rudenok.000webhostapp.com/api/apii.php?${window.location.search.replace("?", "")}&method=theme.set&params=state.vkui.colorScheme`)
.then(res => res.json())
.then(res => { this.setState({theme: res.response})})
window.location.reload()
}
theme1 = () => {
fetch(`https://rudenok.000webhostapp.com/api/apii.php?${window.location.search.replace("?", "")}&method=theme.set&params=client_dark`)
.then(res => res.json())
.then(res => { this.setState({theme: res.response})})
window.location.reload()
}
theme2 = () => {
fetch(`https://rudenok.000webhostapp.com/api/apii.php?${window.location.search.replace("?", "")}&method=theme.set&params=space_gray`)
.then(res => res.json())
.then(res => { this.setState({theme: res.response})})
window.location.reload()
}
theme3 = () => {
fetch(`https://rudenok.000webhostapp.com/api/apii.php?${window.location.search.replace("?", "")}&method=theme.set&params=client_light`)
.then(res => res.json())
.then(res => { this.setState({theme: res.response})})
window.location.reload()
}
theme4 = () => {
fetch(`https://rudenok.000webhostapp.com/api/apii.php?${window.location.search.replace("?", "")}&method=theme.set&params=bright_light`)
.then(res => res.json())
.then(res => { this.setState({theme: res.response})})
window.location.reload()
}

    render() {
        const {id, setPage, withoutEpic, colorScheme} = this.props;
        const boxStyle = {marginTop: 9};

        return (
            <Panel id={id}>
<PanelHeader left={<PanelHeaderBack onClick={this.props.goBack}/>}>Debug</PanelHeader>
<Group title="Themes" description="После выбора темы, у Вас перезагрузится сервис.">
{this.state.theme === 'state.vkui.colorScheme' ? 
<Radio name="radio" value="1" defaultChecked> По умолчанию (vkui.colorScheme) </Radio>:
<Radio name="radio" value="1" onClick={(this.themec)}> По умолчанию (vkui.colorScheme) </Radio>}

{this.state.theme === 'client_dark' ? 
<Radio name="radio" value="1" defaultChecked> Темная тема (client_dark) </Radio>:
<Radio name="radio" value="1" onClick={(this.theme1)}> Темная тема (client_dark) </Radio>}

{this.state.theme === 'space_gray' ? 
<Radio name="radio" value="1" defaultChecked> Космичекая тема (space_gray) </Radio>:
<Radio name="radio" value="1" onClick={(this.theme2)}> Космическая тема (space_gray) </Radio>}


{this.state.theme === 'client_light' ? 
<Radio name="radio" value="1" defaultChecked> Белая тема (client_light) </Radio>:
<Radio name="radio" value="1" onClick={(this.theme3)}> Белая тема (client_light) </Radio>}

{this.state.theme === 'bright_light' ? 
<Radio name="radio" value="1" defaultChecked> Светлая тема (bright_light)</Radio>:
<Radio name="radio" value="1" onClick={(this.theme4)}> Светлая тема (bright_light) </Radio>}
 <Div>
                    </Div>
</Group>
  </Panel>
        );
    }

}


const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab
    };
};

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal,
    setActiveTab,
    setScrollPositionByID
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);
