


import React from 'react';
import {connect} from 'react-redux';
import vkconnect from '@vkontakte/vkui-connect';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";
import Icon28Message from '@vkontakte/icons/dist/28/message';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';
import Icon28MessagesOutline from '@vkontakte/icons/dist/28/messages_outline';
import Icon28UserAddOutline from '@vkontakte/icons/dist/28/user_add_outline';
import Icon24Game from '@vkontakte/icons/dist/24/game';
import {
    Div,
    Panel,
    Group,
    CellButton,
    PanelHeader,
    Alert,
    Switch,
    Footer,
    Avatar,
    Spinner,
    ScreenSpinner,
    FormLayout,
    PanelSpinner,
    FixedLayout,
    Button,
    Cell,
    List,
    Input,
    HorizontalScroll,
    TabsItem,
    Tabs
} from "@vkontakte/vkui";

class HomePanelProfile extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            activeTab: props.activeTab["EXAMPLE"] || "modal",
            result: 0,
            fetchedUser: '',
            popout: null,
            viewLoaded: false,
            value1: '',
            formula: '',
            formula2: '',
            valid: 'default'
        };
    }

onChanges = (e) => {
const { name, value } = e.currentTarget;
this.setState({ [name]: value });
}

 onClick () {
    this.setState({popout: <ScreenSpinner />});
    setTimeout(() => { this.setState({ popout: null }) }, 2000);
  }

    setTab(tab) {
        this.setState({
            activeTab: tab
        });
    }

    openPopoutInternet() {
            this.props.openPopout(
                <Alert
                    actions={[{
                        title: 'Ясно',
                        autoclose: true,
                        style: 'cancel',
                    }]}
                    onClose={() => this.props.closePopout()}
                >
                    <h2>Информация</h2>
                    <p>Не удалось получить данные, возможно, пропало подключение к интернету.</p>
                </Alert>
            );
        }

	    componentWillUnmount() {
        const {setScrollPositionByID, setActiveTab} = this.props;
        setActiveTab("EXAMPLE", this.state.activeTab);
        setScrollPositionByID("EXAMPLE_TABS_LIST");
    }



    componentDidMount() {
        restoreScrollPosition();
vkconnect.send("VKWebAppGetUserInfo", {});
vkconnect.subscribe((e) => {
console.log('subscribe');
switch(e.detail.type) { case 'VKWebAppGetUserInfoResult':
console.log(e.detail.data);
this.setState({fetchedUser: e.detail.data});
break;
 }
 });
}

check = () => {

fetch(`https://rudenok.000webhostapp.com/api/apii.php?${window.location.search.replace("?", "")}&method=solve.uravnenie&formula=${this.state.value1}`)
.then(res => res.json())
.then(res => {
this.setState({formula: res.response, viewLoaded: true, formula2: res.response2})
if(this.state.formula === 'Формула не найдена.'){
this.setState({formula: 'Ошибка.',formula2: 'Ошибка.', valid: 'error'});
}
if(this.state.formula === 'Данные не введены.'){
this.setState({formula: 'Ошибка.',formula2: 'Ошибка.', valid: 'error'});
}
if(this.state.formula2 === ''){
this.setState({formula2: 'Ошибка',formula: 'Ошибка.', valid: 'error'});
}
}
).catch(e => { this.openPopoutInternet() });

}

    render() {
        const {id, setPage, withoutEpic} = this.props;
        const boxStyle = {marginTop: 56};
        const { viewLoaded } = this.state;
        return (
            <Panel id={id}>
                <PanelHeader>Уравнения</PanelHeader>
<Group title="Что это такое?">
<Div style={{ marginTop: -15 }} >
<Cell multiline>Здесь Вы можете решить уравнение реакции.</Cell></Div>
</Group>
<Group title="Определитель" description="Обратите внимание, что регистр важен при поиске, а также сначала должны быть записаны элементы-анионы(+), а затем элементы-катионы(-).">
<Div>
<Input
value={this.state.value1}
type="text"
top="H2+O2"
name="value1"
placeholder="H2+O2"
pattern="^[a-zA-Z]+$"
maxlength="39"
onChange={this.onChanges}
status={this.state.value1 ? 'valid' : this.state.valid}onKeyDown={this.func}
/>
<br/>
{this.state.value1 === '' ?
<Button disabled size="xl" level="commerce">Решить</Button> :
<Button size="xl" level="commerce" onClick={(this.check)}>Решить</Button>}
</Div>
</Group>
{this.state.viewLoaded === true ?
<Group title="Определитель">
<Div>
<Cell><b>Уравнение: </b>{this.state.formula2}<br/><b>Продукт(ы) реакции: </b> {this.state.formula} </Cell>
</Div>
</Group> : <Div/>}
  </Panel>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal,
    setActiveTab,
    setScrollPositionByID
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);
